  font-weight: 400;
  line-height: 1.48;
  letter-spacing: 0em;
  text-align: center;
  color: #000;
  margin: 0;
  padding: 0 0 0 0;
  background: #f7f3f0;

}

*,
*:before,
*:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}



a {
    text-decoration: none;
    cursor: pointer;
    color: inherit;
}

img {
    border: none;
    max-width: 100%;
}

a,
img {
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
    outline: none;
}
.img
{
  display: block;
  width:100%
}
/* video */
.video-container{
  width: 100%;
  height: calc(100vh - 80px);
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.video-container::after,.bg-img::after
{
  position:absolute;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.5);
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  z-index: 2;
} 
.video-container  iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  transform: translate(-50%, -50%);
  z-index: 1;
}
@media (min-aspect-ratio: 16/9) {
  .video-container iframe {
    /* height = 100 * (9 / 16) = 56.25 */
    height: 56.25vw;
  }
}
    
@media (max-aspect-ratio: 16/9) {
  .video-container iframe {
    /* width = 100 / (9 / 16) = 177.777777 */
    width: 177.78vh;
  }
}
