.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  opacity: 0;
  transition: opacity 0.5s;
  z-index: 50;

  justify-content: center;
  align-items: center;
  display: flex;
}

.modal-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}

.innerPop {
  background-color: #fff;
  max-width: 100%;
