  position: relative;
  z-index: 3;
  padding: 20px;
  box-shadow: 5px 7px 7px 6px rgb(0 0 0);
  border-radius: 8px;
}

.input-holder {
  padding-bottom: 30px;
  margin-bottom: 10px;
  position: relative;
  width: 48%;
}

.input-holder span {
  font-size: 12px;
  color: #dc3545;
  position: absolute;
  bottom: 0;
  left: 0;


}
.close {
  position: absolute;
  top: 10px;
  right: 10px;
  margin: 0 auto;
  width: 30px;
  height: 30px;
  display: block;
  text-align: center;
  cursor: pointer;
  font-size: 0;
  z-index: 50;
}

.close:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 2px;
  height: 100%;
  transform: rotate(45deg);
  transform-origin: center;
  -webkit-transform: rotate(45deg);
  -webkit-transform-origin: center;
  background: #000;
}

.close:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 2px;
  height: 100%;
  transform: rotate(-45deg);
  transform-origin: center;
  -webkit-transform: rotate(-45deg);
  -webkit-transform-origin: center;
  background: #000;
}
